.authDiv {
    background-image: url("https://images.unsplash.com/photo-1607827448299-a099b845f076?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1950&q=80");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    -webkit-filter: brightness(120%);
            filter: brightness(120%);
