}

.navProfile {
    position: absolute;
    right: 1rem;
    top: 2.2rem;
    color: #34495e;
    display: inline-block;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 100px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
